body{
  padding: 0;
  margin: 0;
  color: #fff;
  font-size: 15px;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
}
li {
  list-style-type: none;
  color: #8A8888;
  padding-bottom: 5px;
}

ul {
  margin: 0;
  padding: 0;
}
h1{
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
}
h2,h4,h5{
  margin: 0;
  padding-bottom: 10px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 32px;
  letter-spacing: -2px;
}
h3{
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
}
p{
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
  font-weight: 400;
  text-decoration: none;
}
a{
  text-decoration: none;
}
.container{
  background: #122538;
  padding-top: 11px;
  margin: 0 auto;
  padding-left: 146px;
}
.SiteName{
  color: white;
  font-weight: 700;
  font-size: 36px;
  text-decoration: none;
  letter-spacing: -2px;
  padding-top: 10px;
  padding-left: 10px;
}
.menu{
  display: flex;
  font-weight: 700;
}

.GitName{
  position: absolute;
  right: 146px;
  display: flex;
  height: 70px;
}
.git_icon{
  padding-top: 5px;
  height: 50px;
}
.MadeBy{
  margin-right: 20px;
  font-size: 20px;
  padding-top: 35px;
}
.main{
  margin: 0 auto;
  background: #f3f3f3;
  color: #000;
}
.ContainerContent{
  display: flex;
  justify-content: center;
}
.MessageContent{
  padding-top: 80px;
}
.message{
  padding: 55px 25px 23px;
  background: white;
  width: 426px;
  height: 400px;
  border-radius: 50px;
  box-shadow:  20px 20px 60px #d9d9d9,
  -20px -20px 60px #ffffff;
}
.PCImage{
  padding-top: 50px;
}
#i1{
  margin-bottom: 20px;
}
.SiteInfo{
  background: white;
  width: 1190px;
  height: 106px;
  display: flex;
  margin-left: 50px;
  border-radius: 50px;
  box-shadow: inset 20px 20px 60px #bebebe,
  inset -20px -20px 60px #ffffff;
}
.H1Info{
  font-size: 24px;
  padding-top: 24px;
  padding-left: 20px;
}
.OtherInfo{
  font-size: 16px;
  padding: 20px 25px
}
.InfoContainer{
  display: flex;
  justify-content: center;
  background: #122538;
  padding: 10px 0 10px 50px;
  margin: 0 auto;
}
.Info{
  width: 1190px;
}
.f1{
  font-size: 15px;
  padding-bottom: 15px;
}
.f2{
  color: #8A8888;
}
.Info{
  display: flex;
  justify-content: space-between;
}

.MyLinks{
  color: #8A8888;
}
.MyLinks:hover{
  color: #afacac;
}
.imgs{
  padding-top: 20px;
}
.prev_image{
  height: 75px;
  border-radius: 50px;
  background: #dfdfdf;
  box-shadow:  20px 20px 60px #bebebe,
  -20px -20px 60px #ffffff;
}
.Li-Images{
  display:inline-block;
  padding-right: 58px;
  padding-bottom: 30px;
}
.Li-Images2{
  display:inline-block;
  padding-right: 70px;
  padding-bottom: 30px;
}
.prev_image:hover{
  background: linear-gradient(145deg, #c9c9c9, #efefef);
  box-shadow:  20px 20px 60px #bebebe,
  -20px -20px 60px #ffffff;
  animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
.prev_image:active{
  background: linear-gradient(145deg, #c0bfbf, #efefef);
  box-shadow:  20px 20px 60px #b6b6b6,
  -20px -20px 60px #ffffff;
}
.Best-sellers{
  margin-top: 20px;
  background: black;
  padding-top: 50px;
}
.sellers-items{
  display: grid;
  margin: 70px;
  margin-bottom: 0;
  padding-bottom: 20px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 20px;
  transform: translate(-120%,0px);
  opacity: 0;
  transition: all 0.8s ease 0s;
}
.sellers-items.active{
  opacity: 1;
  transform: translate(0px,0px);
}
.seller-images{
  height: 200px;
  border-radius: 10px;
}
.seller-images:hover{
  animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
.items{
  width: 200px;
}
.items h2{
  color: white;
  font-size: 23px;
  padding: 10px;
}
.sellers-text{
  padding-left: 50px;
}
.Sellers-Name h1{
  padding-left: 100px;
  color: white;
  font-size: 50px;
}
.Sellers-Name{
  opacity: 0;
  transition: all 0.8s ease 0s;
}
.Sellers-Name.active{
  opacity: 1;
}
.sellers-text h3{
  color: #eeeded;
  padding-left: 35px;
  font-weight: 400;
}
.btn{
  border-radius: 50px;
  background: #950aa5;
  padding: 10px 70px;
  margin: 10px 70px 10px 20px;
  color: white;
  height: 25px;
  transition: 150ms linear;
}
.btn h2{
  font-size: 20px;
}
.btn:hover{
  background: #81088d;
  color: #cecece;
}